.grid {
  margin: 10px;
}

.row {
  display: -webkit-flex;
  display: flex;
}

.cell {
  width: 10px;
  height: 10px;
  margin: 1px;
  background-color: white;
  cursor: pointer;
}

.cell:hover {
  border-color: black;
  background-color: lightgray;
}

.cell.active {
  background-color: black;
}

.cell.active:hover {
  border-color: white;
  background-color: #444444;
}

.controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 10px;
  font-size: 20px;
  text-align: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: white;
}

.controls-panel {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.controls-button {
  background-color: #31596c;
  border: 0;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  margin: 10px;
  padding: 10px;
  font-size: 20px;
}

.controls-button:hover {
  background-color: #46a249;
}

.controls-button:active {
  background-color: #3c893e;
}

.controls-field {
  font-size: 20px;
  width: 100px;
  border-radius: 10px;
  border: 0;
  padding: 5px;
  text-align: center;
  margin-bottom: 15px;
  margin-top: 5px;
}

html, body {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  font-family: sans-serif;
}

.app {
  display: -webkit-flex;
  display: flex;
  border-radius: 10px;
  background-color: #607d8b;
}

